.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mugshot {
  width: 30%;
  height: 30%;
}

.contact-info {
  text-align: center;
  width: 40%;
}

/* .contact-info > tr > td {
  border: slategray;
  border-style: solid;
  border-width: 1px;
} */

.contact-info > tr > td:first-child {
  width: 30%;
  text-align: right;
}

.contact-info > tr > td:nth-child(2) > a {
  color: slategray !important;
  text-decoration: underline;
  text-decoration-color: slategray !important;
  text-align: center;
}
